  padding: 14px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.header-home:before {
  content: "";
  z-index: 1;
  background-color: rgba(0, 0, 0, .3);
  position: absolute;
  top: 0;
  bottom: 0;
