.footer-container {
  width: 100%;
  color: #080808;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-top: auto;
  padding: 20px;
  font-family: Roboto;
  font-size: 14px;
  display: flex;
}

footer {
  width: 100%;
  background-color: #f7f7f7;
}

.text-direction {
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-left: 10px;
  display: flex;
}

.heart {
  color: #b92f2c;
  width: 50px;
  height: 25.76px;
  flex-shrink: 0;
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }
}

.students {
  color: #b92f2c;
  cursor: pointer;
  margin-bottom: 0;
  font-weight: bold;
  text-decoration: underline;
}

.rights-reserved {
  margin-bottom: 0;
}

.footer-modal {
  z-index: 12;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .8);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.footer-modal-content {
  width: 80%;
  max-height: 80vh;
  max-width: 600px;
  color: #fff;
  background: rgba(32, 32, 32, .7);
  border: 2px outset #b92f2c;
  border-radius: 10px;
  padding: 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
  box-shadow: 0 10px 30px #b92f2c;
}

.title-btn-container {
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  margin-bottom: 15px;
  display: flex;
}

.footer-modal-title {
  font-size: 18px;
}

.close-button-member {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.close-button-member svg {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.close-button-member:hover, .close-button-member:focus {
  scale: 1.2;
}

.group-section {
  flex-direction: column;
  gap: 20px;
  padding: 0;
  display: flex;
}

.member-section {
  height: 80px;
  background-color: rgba(185, 47, 44, .5);
  border: 1px solid #fff;
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
}

.member-title, .member-title1 {
  color: #fff;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 500;
}

.last-image {
  width: 60px;
  border: 2px solid #f0eae6;
  border-radius: 50%;
}

.member-section:hover .last-image {
  border: 3px solid #b92f2c;
}

.member-section:hover {
  color: #b92f2c;
  background-color: rgba(255, 255, 255, .9);
}

.member-section:hover .member-description, .member-section:hover .member-name {
  color: #b92f2c;
}

.member-section:hover .social-links a {
  fill: #b92f2c;
}

.member-links svg {
  width: 30px;
  height: 30px;
  transition: fill .5s;
}

.member-links .social-links a {
  cursor: pointer;
  fill: #fff;
}

.member-links svg:hover {
  fill: #000;
}

.member-section {
  transition: box-shadow .3s, transform .3s;
}

.member-section:hover {
  transform: scale(1.05);
  box-shadow: 0 0 15px 10px rgba(185, 47, 44, .5);
}

.footer-modal-title:hover:after {
  left: 100%;
  transform: skewX(50deg)skewY(10deg);
}

.footer-modal-title:before, .footer-modal-title:after {
  content: "";
  width: 100%;
  height: 200%;
  pointer-events: none;
  background: rgba(255, 255, 255, .3);
  transition: all .9s;
  position: absolute;
  top: 0;
}

.footer-modal-title:before {
  left: -500%;
}

.footer-modal-title:after {
  right: -400%;
}

@media (max-width: 500px) {
  .footer-modal-content {
    width: 90%;
  }

  .member-section {
    padding: 0;
  }

  .member-name, .member-description {
    font-size: 12px;
  }

  .member-name {
    margin: 0;
  }

  .member-description {
    line-height: 50%;
  }
}

@media (min-width: 768px) {
  .group-section {
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    display: grid;
  }

  .title-btn-container {
    margin-bottom: 50px;
  }

  .footer-modal-title {
    font-size: 28px;
  }

  .footer-modal-content {
    max-width: 800px;
    height: 500px;
  }

  .member-section {
    height: auto;
  }

  .member-links {
    flex-direction: column;
    gap: 10px;
    display: flex;
  }
}

@media (min-width: 1280px) {
  .group-section {
    gap: 30px;
  }

  .footer-modal-content {
    max-width: 1000px;
  }
}

* {
  margin: 0;
  padding: 0;
}

:root {
  --background-color-light: #fff;
  --background-color-dark: #000;
  --text-color-light: #000;
  --text-color-dark: #fff;
  --border-hover-color: #b92f2c;
}

.container {
  max-width: 1280px;
  height: 100%;
  background-color: #f5e8e8;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 32px;
  display: flex;
}

.movie-wrapper {
  background-color: var(--background-color-light);
  background-color: #f5e8e8;
  grid-template-columns: 1fr;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  display: grid;
}

.movie-wrapper__card {
  background-color: var(--background-color-light);
  cursor: pointer;
  width: 280px;
  height: auto;
  background-color: #f5e8e8;
  border-radius: 10px;
  flex-shrink: 0;
  transition: border .3s, box-shadow .3s;
  position: relative;
  overflow: hidden;
}

.movie-wrapper__card:hover {
  border: 1px solid var(--border-hover-color);
  box-shadow: 0 6px 22px var(--border-hover-color);
  background-color: #f5e8e8;
  scale: 1.02;
}

.movie-wrapper__card-img img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.movie-wrapper__footer {
  color: var(--text-color-light);
  padding: 10px;
}

.movie-wrapper__title {
  text-transform: uppercase;
  margin: 5px 0;
  font-size: 16px;
  font-weight: 500;
}

.movie-wrapper__info {
  color: #b92f2c;
  font-size: 14px;
  font-weight: 500;
  line-height: 16px;
}

.movie-wrapper__info-rating {
  color: #fff;
  font-size: 24px;
  font-weight: 500px;
  background-color: #b92f2c;
  border-radius: 4px;
  margin-left: 4px;
  padding: 2px 6px;
  display: inline-block;
  position: absolute;
  top: 5%;
  left: 5%;
}

@media (min-width: 768px) {
  .movie-wrapper {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }

  .movie-wrapper__card-img img {
    width: 336px;
    height: 455px;
  }

  .movie-wrapper__card {
    width: 336px;
  }
}

@media (min-width: 1280px) {
  .movie-wrapper {
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;
  }

  .movie-wrapper__info {
    font-size: 20px;
    font-weight: 500;
    line-height: 24px;
  }

  .movie-wrapper__card-img img {
    width: 395px;
    height: 574px;
  }

  .movie-wrapper__card {
    width: 395px;
  }
}

